import React, { useState, useEffect } from 'react';
import './AdminAuth.css';

interface AdminAuthProps {
  onAuthSuccess: () => void;
}

const AdminAuth: React.FC<AdminAuthProps> = ({ onAuthSuccess }) => {
  const [otp, setOtp] = useState('');
  const [generatedOtp, setGeneratedOtp] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');
  const [otpSent, setOtpSent] = useState(false);

  // OTP   Telegram 
  const generateAndSendOTP = async () => {
    setIsLoading(true);
    setError('');

    // 6  OTP 
    const newOtp = Math.floor(100000 + Math.random() * 900000).toString();
    setGeneratedOtp(newOtp);

    //   OTP  (5 )
    sessionStorage.setItem('adminOtp', newOtp);
    sessionStorage.setItem('otpExpiry', (Date.now() + 5 * 60 * 1000).toString());

    // Telegram OTP 
    try {
      const botToken = process.env.REACT_APP_TELEGRAM_BOT_TOKEN;
      const chatId = process.env.REACT_APP_TELEGRAM_CHAT_ID;

      if (!botToken || !chatId) {
        throw new Error('Telegram  ');
      }

      const message = ` DIORA   \n\n : ${newOtp}\n\n 5  \n    `;

      const response = await fetch(
        `https://api.telegram.org/bot${botToken}/sendMessage`,
        {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            chat_id: chatId,
            text: message,
            parse_mode: 'HTML'
          })
        }
      );

      if (!response.ok) {
        throw new Error('OTP  ');
      }

      setOtpSent(true);
      setIsLoading(false);
    } catch (err) {
      setError('OTP    ');
      setIsLoading(false);
      //    OTP 
      console.log('Development OTP:', newOtp);
    }
  };

  // OTP 
  const verifyOTP = () => {
    const storedOtp = sessionStorage.getItem('adminOtp');
    const otpExpiry = sessionStorage.getItem('otpExpiry');

    if (!storedOtp || !otpExpiry) {
      setError('OTP  ');
      return;
    }

    if (Date.now() > parseInt(otpExpiry)) {
      setError('OTP .  ');
      sessionStorage.removeItem('adminOtp');
      sessionStorage.removeItem('otpExpiry');
      return;
    }

    if (otp === storedOtp) {
      //  
      sessionStorage.setItem('adminAuthenticated', 'true');
      sessionStorage.setItem('authExpiry', (Date.now() + 30 * 60 * 1000).toString()); // 30 
      sessionStorage.removeItem('adminOtp');
      sessionStorage.removeItem('otpExpiry');
      onAuthSuccess();
    } else {
      setError('  ');
    }
  };

  //   
  useEffect(() => {
    const isAuthenticated = sessionStorage.getItem('adminAuthenticated');
    const authExpiry = sessionStorage.getItem('authExpiry');

    if (isAuthenticated && authExpiry && Date.now() < parseInt(authExpiry)) {
      onAuthSuccess();
    }
  }, [onAuthSuccess]);

  return (
    <div className="admin-auth">
      <div className="auth-container">
        <h1>  </h1>
        <p className="auth-description">
              .
        </p>

        {!otpSent ? (
          <div className="otp-request">
            <p>Telegram   ?</p>
            <button
              onClick={generateAndSendOTP}
              disabled={isLoading}
              className="otp-send-btn"
            >
              {isLoading ? ' ...' : '  '}
            </button>
          </div>
        ) : (
          <div className="otp-verify">
            <p>Telegram  6  </p>
            <input
              type="text"
              value={otp}
              onChange={(e) => setOtp(e.target.value.replace(/\D/g, '').slice(0, 6))}
              placeholder="000000"
              maxLength={6}
              className="otp-input"
            />
            <button
              onClick={verifyOTP}
              disabled={otp.length !== 6}
              className="otp-verify-btn"
            >
              
            </button>
            <button
              onClick={generateAndSendOTP}
              className="otp-resend-btn"
              disabled={isLoading}
            >
               
            </button>
          </div>
        )}

        {error && <p className="error-message">{error}</p>}
      </div>
    </div>
  );
};

export default AdminAuth;