import React, { useState } from 'react';
import { ref, push, serverTimestamp } from 'firebase/database';
import { contactDatabase } from '../../firebaseContact';
import './ContactFormFirebase.css';

const ContactFormFirebase: React.FC = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    content: ''
  });

  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitStatus, setSubmitStatus] = useState<'idle' | 'success' | 'error'>('idle');

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handlePhoneChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    let value = e.target.value.replace(/[^0-9]/g, '');
    let formattedValue = '';

    if (value.length <= 3) {
      formattedValue = value;
    } else if (value.length <= 7) {
      formattedValue = value.slice(0, 3) + '-' + value.slice(3);
    } else if (value.length <= 11) {
      formattedValue = value.slice(0, 3) + '-' + value.slice(3, 7) + '-' + value.slice(7);
    }

    setFormData(prev => ({
      ...prev,
      phone: formattedValue
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);

    try {
      const inquiriesRef = ref(contactDatabase, 'inquiries');
      await push(inquiriesRef, {
        ...formData,
        status: 'pending',
        createdAt: serverTimestamp(),
        userAgent: navigator.userAgent,
        referrer: document.referrer || 'direct'
      });

      setSubmitStatus('success');

      //  
      setFormData({
        name: '',
        email: '',
        phone: '',
        content: ''
      });

      // 3    
      setTimeout(() => {
        setSubmitStatus('idle');
        const closeBtn = document.querySelector('.close-btn') as HTMLButtonElement;
        if (closeBtn) closeBtn.click();
      }, 3000);

    } catch (error) {
      console.error('  :', error);
      setSubmitStatus('error');

      setTimeout(() => {
        setSubmitStatus('idle');
      }, 5000);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="form-container">
      <form id="contactForm" onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="name">
             <span className="required">*</span>
          </label>
          <input
            type="text"
            id="name"
            name="name"
            required
            placeholder="  "
            value={formData.name}
            onChange={handleChange}
          />
        </div>

        <div className="form-group">
          <label htmlFor="email">
              <span className="required">*</span>
          </label>
          <input
            type="email"
            id="email"
            name="email"
            required
            placeholder="  "
            value={formData.email}
            onChange={handleChange}
          />
        </div>

        <div className="form-group">
          <label htmlFor="phone">
             <span className="required">*</span>
          </label>
          <input
            type="tel"
            id="phone"
            name="phone"
            required
            placeholder="    (: 010-1234-5678)"
            pattern="[0-9]{3}-[0-9]{4}-[0-9]{4}|[0-9]{10,11}"
            value={formData.phone}
            onChange={handlePhoneChange}
          />
        </div>

        <div className="form-group">
          <label htmlFor="content">
              <span className="required">*</span>
          </label>
          <textarea
            id="content"
            name="content"
            required
            placeholder="   "
            value={formData.content}
            onChange={handleChange}
          />
        </div>

        <p className="file-info">
          , ,     <strong>dio@diora.co.kr</strong>   
        </p>

        <button type="submit" className="submit-btn" id="submitBtn" disabled={isSubmitting}>
          {isSubmitting ? (
            <>
              <span className="loading"></span>  ...
            </>
          ) : (
            ' '
          )}
        </button>

        {submitStatus === 'success' && (
          <div className="success-message" id="successMessage">
               !<br/>
              24  .
          </div>
        )}

        {submitStatus === 'error' && (
          <div className="error-message" id="errorMessage">
                 .<br/>
               .
          </div>
        )}
      </form>
    </div>
  );
};

export default ContactFormFirebase;