import React, { useState } from 'react';
import ContactFormFirebase from '../ContactFormFirebase/ContactFormFirebase';
import './Hero.css';

const Hero: React.FC = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const openModal = () => {
    setIsModalOpen(true);
    document.body.style.overflow = 'hidden';
  };

  const closeModal = () => {
    setIsModalOpen(false);
    document.body.style.overflow = 'unset';
  };

  const handleModalClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      closeModal();
    }
  };

  return (
    <section id="hero" className="hero">
      <div className="container">
        <div className="hero-content">
          <h1 className="hero-title">
            <span className="title-part-1">{'\u00A0'},</span>{' '}
            <span className="title-part-2">{'\u00A0'}</span>
          </h1>
          <p className="hero-subtitle">
            1{'\u00A0'}{'\u00A0'}{'\u00A0'}{'\u00A0'}
          </p>
          <button className="hero-cta" onClick={openModal}>
            
          </button>
        </div>

        {/* Beautiful Custom Modal */}
        {isModalOpen && (
          <div className="form-modal" onClick={handleModalClick}>
            <div className="modal-content">
              <button className="close-btn" onClick={closeModal} title="">
                
              </button>
              <div className="modal-header">
                <h2> </h2>
                <p>,   </p>
              </div>
              <div className="modal-body">
                <ContactFormFirebase />
              </div>
            </div>
          </div>
        )}
      </div>
    </section>
  );
};

export default Hero;